.footer {
    background-color: $color-grey-dark-3;
    padding: 10rem 0;
    font-size: 1.4rem;

    @include responsive(tablet-portrait) {
        padding: 8rem 0;
    }

    &__container {        
        display: flex;
        justify-content: space-evenly;

        @include responsive(tablet-portrait) {
           flex-direction: column;
           flex-wrap: wrap;
           align-content: center;
        }
    }

    &__logo-box {
       text-align: center;
       margin-bottom: 8rem;

       @include responsive(tablet-portrait) {
            margin-bottom: 6rem;
       }
    }

    &__logo {
        width: 15rem;
        height: auto;
    }

    &__navigation {
        border-top: 1px solid $color-grey-light-2;
        padding-top: 2rem;
        
        @include responsive(tablet-portrait) {
            padding: 2rem 0rem 4rem 0;
        }
    }

    &__list {
        list-style: none;
    }

    &__item {
        display: inline-block;

        &:not(:last-child) {
            margin-right: 1.5rem;
        }
    }

    &__link {
        &:link,
        &:visited {
            color: $color-grey-ligth-1;
            text-decoration: none;
            text-transform: uppercase;
            display: inline-block;
            transition: all .3s;
        }

        &:hover,
        &:active {
            color: $color-primary;
            box-shadow: 0 1rem 2rem rgba($color-black, .4);
            transform: rotate(5deg) scale(1.3);
        }        
    }

    &__copyright {        
        border-top: 1px solid $color-grey-light-2;
        padding-top: 2rem;
    }
}